:root {
  --font-body: Arial, sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --whiteLight: hsl(0, 8%, 75%);
  --black: hsl(0, 0%, 0%);
  --violet: hsl(203, 84%, 17%);
  --gray:hsl(0, 0%, 53%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.grid_2__cols {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.navbar {
  overflow: hidden;
  z-index: 20;
}
.navbar_menu {
  height: 3.125rem;
  width: 3.125rem;
  position: relative;
  transition: 0.4s ease;
}
.navbar_menu__togglebox {
  position: relative;
  display: block;
  margin-left: 3.25rem;
  margin-top: 1.4375rem;
  height: 3.125rem;
  width: 3.125rem;
  border-radius: 3.125rem;
}
.navbar_menu__togglebox .logo {
  position: absolute;
  height: 0.25rem;
  width: 2.1875rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--whiteLight);
  border-radius: 3.125rem;
}
.navbar_menu__togglebox .logo::before, .navbar_menu__togglebox .logo::after {
  position: absolute;
  content: "";
  height: 0.25rem;
  width: 2.1875rem;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--whiteLight);
  border-radius: 3.125rem;
  transition: 0.3s;
}
.navbar_menu__togglebox .logo::before {
  top: 0.75rem;
}
.navbar_menu__togglebox .logo::after {
  bottom: 0.5rem;
}
.navbar_menu .active {
  background-color: var(--white);
  border-radius: 3.125rem;
  border: solid 0.1875rem var(--violet);
}
.navbar_menu .active .logo {
  width: 0;
}
.navbar_menu .active .logo::before {
  background-color: var(--violet);
  width: 2rem;
  transform-origin: right;
  transform: translate(-20px, 0px) rotate(45deg);
}
.navbar_menu .active .logo::after {
  background-color: var(--violet);
  width: 2rem;
  transform-origin: right;
  transform: translate(-20px, -2px) rotate(-45deg);
}
.navbar_menu__profil {
  text-align: center;
  margin: 0.9375rem 0 2.8125rem;
}
.navbar_menu__items {
  width: 100%;
}
.navbar_menu__items li a {
  color: var(--black);
}
.navbar_menu__items li a .item {
  border-bottom: solid 0.0625rem;
  padding-bottom: 0.6875rem;
}
.navbar_menu__items li a span {
  position: relative;
  display: block;
  letter-spacing: 2;
  padding-left: 1rem;
  margin-top: 1.3125rem;
  width: 100%;
}
.navbar .block {
  margin: 0;
  height: 100vh;
  width: 25%;
  background-color: var(--white);
}
@media (max-width: 26.563em) {
  .navbar .block {
    width: 100%;
  }
}

.histoire {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background-color: var(--gray);
}
.histoire_book__content {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 65%;
  min-width: 15.625rem;
  max-width: 25rem;
  height: 20.625rem;
  position: relative;
  perspective: 62.5rem;
  transition: 1s;
}
.histoire_book__content .book {
  position: absolute;
  width: 65%;
  height: 100%;
  transition: 1s;
  transform-style: preserve-3d;
  transform-origin: left;
}
.histoire_book__content .book .face-front, .histoire_book__content .book .face-back {
  width: 100%;
  height: 100%;
  background-color: var(--white);
  padding: 0.9375rem;
  box-sizing: border-box;
  overflow: hidden;
}
.histoire_book__content .book .face-front {
  box-shadow: inset 0.1875rem 0 1.25rem -0.4375rem var(--black);
  border-top-right-radius: 0.1875rem;
  border-bottom-right-radius: 0.1875rem;
}
.histoire_book__content .book .face-back {
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: inset 0.1875rem 0 1.25rem -0.4375rem var(--black);
  transform: translateZ(-1px) rotateY(180deg);
  border-top-left-radius: 0.1875rem;
  border-bottom-left-radius: 0.1875rem;
}
.histoire_book__content .book .face-back p {
  margin-top: 0.625rem;
  font-size: 0.875rem;
  text-align: justify;
}
@media (max-width: 50em) {
  .histoire_book__content .book .face-back p {
    font-size: 0.75rem;
  }
}
.histoire_book__content .book #portada {
  background-color: #7070f0;
}
.histoire_book__content .book #portada-back {
  background-color: #7070f0;
}
.histoire_book__content .book #portada, .histoire_book__content .book #portada-back {
  background-size: 100% 100%;
}

.trnsf {
  transform: translateX(5rem);
}

.trnsf-reset {
  transform: translateX(0rem);
}